import { Component, h, Element, Prop } from '@stencil/core';

@Component({
  tag: 'ks-order-toggle-section',
})
export class OrderToggleSection {

    @Prop() left: string;
    @Prop() right: string;
    @Prop() lValue: string;
    @Prop() rValue: string;

    @Prop() name: string;

    @Element() root: HTMLElement;

    @Prop({mutable: true}) value: string = "left";

    ChangeHandler() {
        const input = this.root.querySelector(`ks-order-toggle input[name='${this.name}']:checked`) as HTMLInputElement;
        this.value = input.value == this.lValue ? "left" : "right" ;
    }

    componentDidLoad(){
        this.root.style.display = "block";
        this.ChangeHandler();
    }

    render() {
        return (
            <div>
                <div class="uk-child-width-1-2 uk-flex">
                    <ks-order-toggle active={this.value == "left"} text={this.left} name={this.name} value={this.lValue} onClick={()=>this.ChangeHandler()}></ks-order-toggle>
                    <ks-order-toggle active={this.value == "right"} text={this.right} name={this.name} value={this.rValue} onClick={()=>this.ChangeHandler()}></ks-order-toggle>
                </div>
                <br />

                <div hidden={this.value != "left"}>
                    <slot name="left" />
                </div>
                
                <div hidden={this.value != "right"}>
                    <slot name="right" />
                </div>
            </div>
        )
    }
}